@import "bourbon";
@import "fonts";
@import "vars";




* {

  font-family: $default_font; }
label {
  font-weight: inherit; }

body {
  //display: none
  font-size: 17px;
  color: white;
  line-height: 21px;
  font-weight: 500; }

button {
  padding: 0;
  border: none;
  font: inherit;
  cursor: pointer;
  &:focus {
    outline: none; } }

input, select {
  border: none;
  margin: 0;
  outline: none; }


input[type=file]:focus,
input[type=checkbox]:focus,
input[type=radio]:focus {
  outline: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0; }


input[type=number] {
  -moz-appearance: textfield; }

input[type=checkbox],
input[type=radio] {
  margin: 0; }

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr,
acronym, address, big, cite, code, del, dfn, em, img, ins, kbd,
q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i,
center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td, article, aside,
canvas, details, embed, figure, figcaption, footer, header,
hgroup, menu, nav, output, ruby, section, summary, time, mark,
audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline; }

ul {
  margin: 0;
  padding: 0;
  list-style: none; }
a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
  &:hover {
    text-decoration: none;
    color: inherit; }
  &:focus {
    outline: none;
    text-decoration: none;
    color: inherit; } }

header {
  background: #1F2431; }

.button {
  min-width: 120px;
  padding: 12px 22px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 700;
  transition: all .3s ease-in;
  cursor: pointer;
  display: inline-flex;
  align-self: center;
  justify-content: center;
  color: #ffffff; }

.button-primary {
  background-color: #315EFB;
  &:focus {
    background-color: #2997FF; }
  &[disabled] {
    background-color: #62687F;
    cursor: not-allowed; } }

.button-secondary {
  background-color: #000000;

  &:hover {
    background-color: #343b4c; }
  &:focus {
    background-color: #343B4C; }
  &[disabled] {
    background-color: #62687F;
    cursor: not-allowed; } }

.top-line {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #757575; }

.top-line__headerTypeTwo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #757575;
  padding: 25px 0; }



.top-line__left-lang {
  color: $secondary_color;
  margin-right: 27px;
  font-weight: 700;
  padding-top: 2px;
  transition: all 0.3s ease-in-out;
  &:hover {
    color: #EEF2F4; } }

// Choose language =========
.textTransform {
  text-transform: uppercase; }

.dropdown-menu {
  left: -10px;
  margin-top: 10px;
  min-width: 75px;
  li {
    a {
      padding-left: 12px; } } }

// ====================

.top-line__menu-item {
  display: inline-block;
  margin: 0 19px; }

.top-line__menu-link {
  position: relative;
  display: inline-block;
  padding: 24px 0;
  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    opacity: 0;
    width: 100%;
    height: 4px;
    transition: all 0.3s ease-in-out; }
  &:hover {
    -webkit-background-clip: text;
    color: transparent;
    transition: all .3s ease-in; }
  &:hover:after {
    opacity: 1; } }

.top__link-Home {
  &:after {
    background-image: $orangeToPink; }
  &:hover {
    background-image: $orangeToPink; } }

.top__link-Business {
  &:after {
    background-image: $blueToPurple; }
  &:hover {
    background-image: $blueToPurple; } }

.top__link-Magazine {
  &:after {
    background-color: #1ED760; }
  &:hover {
    color: #1ED760; } }

.more {
  position: relative;
  color: #8D96B2;
  cursor: pointer;
  display: inline-block;

  &:hover {
    .more__dropdown {
      display: block; } } }

.more__text {
  padding: 24px 0;
  transition: all .3s ease-in;
  &:hover {
    color: #ffffff;
    .dots {
      fill: #ffffff; } } }


.more__dropdown {
  display: none;
  position: absolute;
  background-color: #343B4C;
  min-width: 270px;
  padding: 26px 39px;
  border-radius: 12px;
  &-item {
    display: block;
    transition: all .3s ease-in;
    &:hover {
      color: #ffffff; }
    & + .more__dropdown-item {
      margin-top: 5px; } } }

.dots {
  vertical-align: middle;
  margin-left: 5px;
  width: 22px;
  height: 6px;
  fill: $secondary_color;
  transition: all .3s ease-in; }

.tools {
  &:focus {
    color: inherit; }
  &:hover {
    color: #ffffff;
    .personal__icon {
      stroke: #ffffff; } } }

.tools__item {
  display: inline-block;
  vertical-align: middle;
  line-height: 25px;
  color: $secondary_color;
  transition: all .3s ease-in; }


.personal__icon {
  width: 21px;
  height: 27px;
  stroke: $secondary_color;
  fill: transparent;
  vertical-align: bottom;
  transition: all .3s ease-in; }


// SWIPE RIGHT MENU START
.menu__bg {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 500;
  background-color: rgba(0, 0, 0, 0.3); }

.burger {
  display: none;
  cursor: pointer; }

.burger-item {
  width: 25px;
  height: 3px;
  background-color: #ffffff;
  margin: 5px; }

.right-menu__close {
  padding: 30px;
  position: absolute;
  top: 0; }

.right-menu {
  display: none;
  position: fixed;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 1000;
  width: 330px;
  background-color: #1F2431;
  transform: translateX(105%);
