.right-menu__btn {
  width: 21px;
  height: 21px;
  background: none;
  border: none;
  background-image: url('/img/icons/CloseMenu.svg'); }

.right-menu__close {
  text-align: left; }

.right-menu-contacts {
  display: none;
  padding: 55px 0 40px;
  text-align: left; }

.right-menu__category {
  font-size: 21px;
  line-height: 26px;
  margin: 0 0 36px; }

.category__item {
  transition: all .2s ease-in; }

.category__item + .category__item {
  margin-top: 18px; }

.right-menu__col-home {
  &:hover {
    background-image: $orangeToPink;
    -webkit-background-clip: text;
    color: transparent; } }

.right-menu__col-business {
  &:hover {
    background-image: $blueToPurple;
    -webkit-background-clip: text;
    color: transparent; } }

.right-menu__col-magazine {
  &:hover {
    color: #1ED760; } }

.right-menu__col-other {
  color: $secondary_color;
  &:hover {
    color: #ffffff; } }

.right-line__link {
  display: flex;
  font-size: 17px;
  line-height: 21px;
  &:hover {
    .right-line__link-text {
      color: #ffffff; }
    .right-line__icon {
      fill: white; }
    .right-line__icon-phone {
      stroke: #ffffff; }
    .icon-phone {
      stroke: white; }
    .icon__VOLS {
      fill: transparent;
      stroke: #ffffff; } } }

.right-menu__list-item + .right-menu__list-item {
  margin-top: 25px; }

.right-line__icon {
  transition: all .2s ease-in;
  fill: $secondary_color;
  width: 25px;
  height: 21px;
  margin-right: 18px; }

.right-line__link-text {
  transition: all .2s ease-in;
  color: $secondary_color; }

.icon-phone {
  transition: all .2s ease-in;
  fill: none;
  stroke: $secondary_color;
  transition: all .2s ease-in;
  width: 25px;
  height: 21px;
  margin-right: 18px; }


.icon-wi-fi {
  width: 21px;
  height: 27px; }

// SWIPE RIGHT MENU END

.middle-line {
  display: flex;
  justify-content: space-between;
  padding: 48px 15px 47px 15px;
  &__wrapper-left {
    width: 55%;
    display: flex;
    align-items: center; }
  &__wrapper-right {
    display: flex;
    align-items: center; } }

.logo {
  margin-right: 15px;
  max-width: 254px;
  width: 100%;
  height: 46px; }


.middle-line__email {
  line-height: 24px;
  color: $secondary_color;
