  &:hover {
    color: transparent; } }

.telegMarg {
  padding-right: 3px; }

.bottom-line-fixed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5000;
  background-color: #1f2431;
  padding: 5px 0;
  opacity: 0.9; }

.bottom-line__list {
  padding: 10px 0 11px;
  display: flex;
  justify-content: center; }

.bottom-line__link {
  display: inline-block;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  &:hover {
    .bottom-line__link-text {
      color: #ffffff; }
    .bottom-line__icon {

      fill: white; }
    .bottom-line__icon-phone {
      stroke: #ffffff; }
    .icon__VOLS {
      fill: transparent;
      stroke: #ffffff; } } }


.bottom-line__list-item+.bottom-line__list-item {
  margin-left: 38px; }

.bottom-line__icon {
  transition: all .2s ease-in;
  fill: $secondary_color;
  width: 37px;
  height: 27px;
  margin-bottom: 10px; }

.bottom-line__link-text {
  transition: all .2s ease-in;
  display: block;
  font-size: 14px;
