  height: 27px;
  margin-bottom: 10px; }

.icon__VOLS {
  fill: transparent;
  stroke: $secondary_color;
  &:hover {
    fill: transparent;
    stroke: #ffffff; } }

// SLIDER START

.promotion-slider__item {
  position: relative;
  min-height: 600px;
  width: 195px; }

.promotion-slider__img {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  right: 0;
  margin: 0 auto;
  object-fit: cover; }

.slick-track {
  display: flex; }

.promotion-slider {
  position: relative;
  background-color: #1f2431;
  .slick-dots {
    position: absolute;
    bottom: 20px;
    left: 50%;
    right: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    .slick-active {
        button {
          background-color: rgba(255, 255, 255, 100); } }
    li {
      list-style: none;
      padding-right: 8px; }
    button {
      bottom: 42px;
      left: 50%;
      font-size: 0;
      width: 15px;
      height: 15px;
      background-color: rgba(255, 255, 255, 0);
      border: 2px #ffffff solid;
      border-radius: 50%;
      &:focus {
        outline: none; } } } }

.slick-slide:active {
  outline: none; }

.slick-active {
  outline: none; }

// SLIDER END

