.popup__content p {
  margin-bottom: 25px; }

.popup {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
  background: rgba(#1F2431, .5); }



.popup__body {
  position: absolute;
  max-width: 900px;
  width: 100%;
  min-height: 400px;
  max-height: 500px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 25px;
  background-color: #1F2431;
  color: $secondary_color;
  border-radius: 36px;
  overflow-y: auto;
  overflow-x: hidden; }

.popup__content {
  padding-left: 40px;
