.popup__close {
  position: absolute;
  top: 30px;
  right: 30px;
  width: 23px;
  height: 23px;
  background: none;
  cursor: pointer; }

.popup__close:before {
  content: '';
  background-color: $secondary_color;
  position: absolute;
  height: 2px;
  width: 12px;
  top: 11px;
  left: -4px;
  transform: rotate(-45deg); }

.popup__close:after {
  content: '';
  background-color: $secondary_color;
  position: absolute;
  height: 2px;
  width: 12px;
  top: 11px;
  left: -4px;
  transform: rotate(45deg); }

