.phone-col-bussines-3:before {
  background: url("../img/icons/phone-icon03.svg") no-repeat; }

.phone__col-title {
  color: #1d1d1f;
  line-height: 17px;
  margin-bottom: 16px; }
.phone__col-text {
  color: #62687F;
  font-size: 14px;
  line-height: 17px; }

.colocation-info {
  padding: 40px 0 45px; }

.colocation-info__title {
  color: #000;
  margin-bottom: 22px;
  text-align: left; }

.colocation-info__subtitle {
  color: #000;
  font-size: 32px;
  line-height: 39px;
  margin-bottom: 25px;
  max-width: 800px;
  width: 100%; }

.colocation-info__text-content {
  color: #62687F;
  font-size: 21px;
  line-height: 26px;
  max-width: 1045px;
  width: 100%; }

.colocation-info__text-block + .colocation-info__text-block {
  margin-top: 30px; }

.colocation-btn {
  margin: 0 auto;
  background: $blueToPurple; }

.colocation-button {
  background-color: #EEF2F4;
  padding: 50px 0; }

.btn__wrap {
  text-align: center; }

.colocation-info-provide {
  padding: 48px 0 34px;
  color: #000; }

.colocation-info-provide__title {
  text-align: left;
  margin-bottom: 24px; }

.colocation-info-provide__inner {
  display: flex; }
.colocation-info-provide__list-item {
  list-style: disc; }

.colocation-info-provide__list1 {
  padding-left: 50px;
  margin-right: 220px;
  max-width: 290px;
  width: 100%; }

.colocation-info-provide__list2 {
  max-width: 320px;
  width: 100%; }

.colocation-info-services__title {
  background-image: $orangeToPink;
  -webkit-background-clip: text;
  color: transparent;
  margin-bottom: 41px; }

.colocation-info-provide__list1 {
  .colocation-info-provide__list-item {
    & + .colocation-info-provide__list-item {
      margin-top: 27px; } } }

.colocation-info-provide__list2 {
  .colocation-info-provide__list-item + .colocation-info-provide__list-item {
    margin-top: 48px; } }


.colocation-info-services {
  background-color: #1F2431;
  padding: 40px 0 0; }

.colocation-info-services__inner {
  flex-wrap: wrap;
  display: flex;
  justify-content: space-between; }

.colocation-info-services__item {
  margin-bottom: 40px; }
.colocation-info-services__text {
  font-size: 32px;
  line-height: 39px;
  font-weight: 700;

  margin-bottom: 13px; }

.colocation-info-services__text1 {
  max-width: 207px;
  width: 100%; }

.colocation-info-services__text2 {
  max-width: 320px;
  width: 100%; }


.colocation-info-services__text3 {
  max-width: 252px;
  width: 100%; }

.colocation-info-services__img {
  margin-bottom: 15px; }
.colocation-info-services__link {
  position: relative;
  color: #2997FF;
  font-size: 21px;
  line-height: 26px;
  &:after {
    padding-left: 5px;
    content: url("../img/arr_link.svg");
    height: 13px;
    width: 9px; }
  &:hover {
    color: #2997FF;
    text-decoration: underline; }
  &:focus {
    text-decoration: none;
    color: #2997FF; } }

.colocation-info-param {
  padding: 50px 0 0; }

.colocation-info-param__title {
  color: #000;
  text-align: left;
  margin-bottom: 23px; }

.colocation-info-param__inner {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-left: 42px; }

.colocation-info-param__item {
  margin-bottom: 30px;
  max-width: 33%;
  width: 100%; }

.param__item {
  display: flex;
  flex-direction: column;
  align-items: flex-end; }

.param__item-w {
  max-width: 236px;
  width: 100%; }
.colocation-info-param__item-title {
  color: #000;
  font-size: 26px;
  line-height: 32px;
  font-weight: 700; }

.colocation-info-param__list {
  color: #62687F;
  font-size: 16px;
  line-height: 20px;
  list-style: auto; }

.colocation-info-param__list-item:before {
  display: inline-block;
  content: "-";
  margin-left: -11px; }

.SLA {
  padding: 108px 0; }

.SLA__inner {
  max-width: 935px;
  width: 100%;
  margin: 0 auto;
  text-align: center; }

.SLA__title {
  color: #000;
  margin-bottom: 19px; }

.SLA__text {
  color: #62687F;
  font-size: 32px;
  line-height: 39px;
  margin-bottom: 27px; }

.phone-tariff__content {
  padding: 39px 0 45px;
  color: #000; }
.phone-tariff__title {
  max-width: 838px;
  width: 100%;
  margin: 32px auto 0;
  text-align: center;
  span {
    background-image: $orangeToPink;
    -webkit-background-clip: text;
    color: transparent; } }

.phone-tariff__name {
  font-size: 32px;
  line-height: 35px;
  text-align: center;
  margin-bottom: 40px; }

.phone-tariff__list {
  max-width: 642px;
  width: 100%;
  margin: 0 auto;
  font-size: 21px;
  line-height: 26px; }

.phone-tariff__list-item {
  position: relative;
  max-width: 700px;
  width: 100%;
  margin: 0 auto;
  padding-left: 35px;
  & + .phone-tariff__list-item {
    margin-top: 27px; }
  &:before {
    content: '';
    background: url("/img/tariff_icon.svg");
    width: 21px;
    height: 16px;
    position: absolute;
    top: 3px;
    left: 0; }
  span {
    background-image: $orangeToPink;
    -webkit-background-clip: text;
    color: transparent; } }

.phone-tariff__card-wrapper {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -85px; }

.phone-tariff__card-item {
  flex: 0 1 33.333%;
  display: flex;
  flex-direction: column;
  padding: 0 85px;
  margin-bottom: 30px; }
.img-wrap {
  text-align: center;
  margin-bottom: 22px; }

.phone-tariff__card-title {
  flex-grow: 1;
  font-size: 21px;
  color: #000;
  margin-bottom: 9px; }

.phone-tariff__card-text {
  color: #62687F;
  font-size: 14px;
  line-height: 17px; }

@import "media";





